import React, { Fragment } from 'react';
import { Link } from 'react-router-dom';

const Home = () => {
    return (
        <Fragment>
            <div className='leftpanel home'>
                <h1>Hi, I'm David</h1>
                <h2><span style={{color: "#3cdbc0"}}>S</span>oftware Tester</h2>
                <h2>Web <span style={{color: "#3cdbc0"}}>D</span>eveloper</h2>
                <h2>Game <span style={{color: "#3cdbc0"}}>E</span>xperimentator</h2>
                <Link to='/about'><button className="aboutbtn">About me</button></Link>
            </div>
            <div className='rightpanel'>
                <h1 className="fadeIn">welcome to my humble portfolio</h1>
            </div>
        </Fragment>
    )
}

export default Home;
