import React, { Fragment } from 'react';

const Skills = () => {
    return (
        <Fragment>
            <div className='leftpanel skills'>
                <h1>My skills</h1>
                <h2>Pretty comfortable with</h2>
                    <ul>
                        <li><i className="fas fa-angle-double-right"></i> HTML</li>
                        <li><i className="fas fa-angle-double-right"></i> CSS (pure, Grid and Bootstrap)</li>
                        <li><i className="fas fa-angle-double-right"></i> JavaScript (except asynchronous)</li>
                        <li><i className="fas fa-angle-double-right"></i> Unity Engine</li>
                    </ul>
                
                <h2>Doing good but still need to improve</h2>
                    <ul>
                        <li><i className="fas fa-angle-double-right"></i> Asynchronous JavaScript</li>
                        <li><i className="fas fa-angle-double-right"></i> ReactJS</li>
                        <li><i className="fas fa-angle-double-right"></i> Sass</li>
                        <li><i className="fas fa-angle-double-right"></i> C# (.NET)</li>
                        <li><i className="fas fa-angle-double-right"></i> Git</li>
                    </ul>
                
                <h2>Still need to work on</h2>
                    <ul>
                        <li><i className="fas fa-angle-double-right"></i> NodeJS</li>
                        <li><i className="fas fa-angle-double-right"></i> Mongoose</li>
                        <li><i className="fas fa-angle-double-right"></i> Python</li>
                        <li><i className="fas fa-angle-double-right"></i> Databases in general</li>
                        <li><i className="fas fa-angle-double-right"></i> Unreal Engine</li>
                    </ul>
                <h2>I'm also expert at googling stuff</h2>    
            </div>
            <div className='rightpanel'>
                <i className="fab fa-html5"></i>
                <i className="fab fa-react"></i>
                <i className="fab fa-git-alt"></i>
                <i className="fab fa-node"></i>
                <i className="fab fa-unity"></i>
                <i className="fab fa-stack-overflow"></i>                    
            </div>
        </Fragment>
    )
}

export default Skills;
