import React, { Fragment } from 'react'

const About = () => {
    return (
        <Fragment>
            <div className='leftpanel skills'>
                <h1>A few words about me...</h1>
                <p>I'm a self taught developer from Czech republic currently working as a Software tester in chill company in Ostrava.</p>
                <br />
                <p>Over the years I've honed my JavaScript as well as learnt its frameworks and libraries.</p>
                
                <p>I have experience mostly with web development, but I'm no newbie in game development and game design as well. That includes mostly Unity and Unreal Engine.</p>

                <h2>I've worked with many tools and tech, but my most favorite are:</h2>
                <ul>
                    <li><i className="fas fa-angle-double-right"></i> <span className="greenspan">Editor:</span> Visual Studio Code</li>
                    <li><i className="fas fa-angle-double-right"></i> <span className="greenspan">CSS framework:</span> none if possible (Bootstrap if necessary)</li>
                    <li><i className="fas fa-angle-double-right"></i> <span className="greenspan">JS library:</span> ReactJS</li>
                    <li><i className="fas fa-angle-double-right"></i> <span className="greenspan">Tech stack:</span> MERN</li>
                </ul>              
            </div>
            <div className='rightpanel'>
                <h2>Whatever you do, never stop learning.</h2>
            </div>
        </Fragment>
    )
}

export default About;
