import React, { Fragment } from 'react';
import { NavLink } from 'react-router-dom';
import instaimg from './instaimg.jpg';

const Sidebar = () => {
  return (
    <Fragment>
      
      <div className='sidebar sidebar-top'>
        <ul>
          <img src={instaimg} className="instaimg" title="Yup, that's me." alt="Me." />
        </ul>
      </div>
      
      <div className='sidebar sidebar-mid'>
        <ul>
          <li><NavLink exact to="/" activeClassName="active"><span className="material-icons">home</span></NavLink></li>
          <li><NavLink exact to="/about" activeClassName="active"><span className="material-icons">perm_identity</span></NavLink></li>
          <li><NavLink exact to="/skills" activeClassName="active"><span className="material-icons">check_circle_outline</span></NavLink></li>
          <li><NavLink exact to="/showcase" activeClassName="active"><span className="material-icons">work_outline</span></NavLink></li>
        </ul>        
      </div>
      
      <div className='sidebar sidebar-bot'>
        <ul>
          <li>
            <a href="https://www.linkedin.com/in/david-kubos/" className="githubicon" target="_blank" rel="noopener noreferrer"><i className="fab fa-linkedin fa-fw"></i></a>
          </li>
          <li>
          <a href="https://github.com/DavKub" className="githubicon" target="_blank" rel="noopener noreferrer"><i className="fab fa-github fa-fw"></i></a>
          </li>        
        </ul>        
      </div>
    
    </Fragment>
  );
};

export default Sidebar;
